.media{
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
  color: whitesmoke;
}

.media:hover{
   background-color: white;
   color: black;
}

.poster{
 border-radius: 10px;
}

.title{
 width: 100%;
 text-align: center;
 font-size: 17px;
 padding: 8px 0;
}

.subtitle{
 display: flex;
 justify-content: space-between;
 padding-bottom: 3px;
 padding: 0 2px;
}


/*Media queries*/

@media (max-width: 550px) {
 .media{width: 46%;}
}